<template>
  <!--  TODO: обязательно надо добавить тренировки на несколько дней, продумать схему БД-->
  <div class="set" v-for="(set, index) in sets" :key="index">
    <div class="set__title" :data-number="index + 1">
      {{ set.title }}
    </div>
    <div class="sets-item__zones" v-if="set.zones && set.zones.length">
      Рабочая область:
      <span v-for="(zone, index) in set.zones" :key="index">
          {{ zone }}
        </span>
    </div>
    <div class="set__repeat">
      <b>{{ set.repeat }}</b> подходов по <b>{{ set.count }}</b> раз
    </div>
    <div class="set__weight" v-if="set.weight">
      Вес: {{ set.weight }}
    </div>

    <div class="set__show-buttons">
      <div class="set__button"
           @click="showDescription(set)">
        Хочу описание
      </div>
      <div class="set__button"
           @click="showExample(set)">
        {{ !set.showExample ? 'Покажи пример' : 'Скрой пример' }}
      </div>
    </div>

    <div class="set__video" v-if="set.showExample"></div>
  </div>

</template>

<script>

export default {
  name: 'AppSetItem',
  emits: ['showDescription'],
  props: {
    sets: {
      type: Object,
      default: () => {
      }
    }
  },
  methods: {
    showExample(set) {
      set.showExample = !set.showExample;
    },
    showDescription(set) {
      this.$emit('showDescription', set);
    }
  }
}
</script>

